@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  transition: 0.5s ease-in-out;
  z-index: 100;
}

.header .logo {
  font-size: 20px;
  font-weight: bold;
  border: 3px solid black;
  padding: 5px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  color: black;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  font-size: 23px;
  transition: 0.5s ease-in-out;
}

.header .logo i {
  font-size: 15px;
}

.header .fa-align-right {
  margin-top: 10px;
  cursor: pointer;
  display: none;
}

.header .menu_reponsive {
  display: none;
  transition: 0.5s ease-in-out;
  z-index: -100;
}

.header .menu {
  overflow: hidden;
}

.header .menu ul {
  display: flex;
  list-style: none;
}

.header .menu ul li a {
  margin-top: 10px;
  display: block;
  text-decoration: none;
  color: black;
  position: relative;
  transition: 0.5s ease-in-out;
  padding: 5px 10px 5px 10px;
  font-weight: bold;
}

.header .menu ul li a::after {
  content: "";
  position: absolute;
  bottom: -22px;
  left: 0;
  height: 5px;
  background-color: black;
  width: 100%;
  transition: 0.5s ease-in-out;
  opacity: 0;
}

.header .menu ul li a:hover::after {
  bottom: 0px;
  opacity: 1;
}

.effect {
  background: #0084ff;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 20px;
  box-shadow: 0 5px 25px -2px #5c5c5c;
}

.effect .logo {
  color: white !important;
  border: 3px solid white !important;
}

.effect .fa-align-right {
  color: white;
}

.effect .menu {
  color: white;
}

.effect .menu ul li a {
  color: white !important;
}

.effect .menu ul li a::after {
  background-color: white !important;
}

@media (max-width: 767.98px) {
  .header {
    position: relative;
  }
  .header .fa-align-right {
    position: absolute;
    display: block;
    right: 20px;
  }
  .header .menu_reponsive {
    position: absolute;
    background: #0084ff;
    padding: 20px;
    display: block !important;
    position: absolute;
    top: -400px;
    right: 0;
    border-bottom-left-radius: 20px;
  }
  .header .menu_reponsive .menu {
    display: block !important;
  }
  .header .menu_reponsive .menu ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .header .menu {
    display: none;
  }
}
